* {
  box-sizing: border-box;
}

body {
  font-size: 13px;
  line-height: 1.1;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(4, minmax(275px, 1fr));
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  display: grid;
}

.photo-card {
  border: 1px solid #c0bebe;
  border-radius: 5px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__img {
  width: 100%;
  height: 100%;
}

.info {
  display: flex;
}

.info-item {
  text-align: center;
}

.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.search-form {
  background-color: #6e3cca;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.search-form input {
  border-radius: 5px;
}

.search-form button {
  margin-left: 5px;
}

.load-more {
  display: none;
}

.load-more.is-visible {
  display: inline-block;
}

